                    $moment(activity.created_at).format("DD-MM-YYYY hh:mmA")
                  }}.
                </p>
              </div>
            </div>
          </div>
        </div>

        <div class="col-lg-4 col-md-cu-4 mb-3">
          <div class="box">
            <label for="" class="custom-label md-text">Date of order</label>
            <el-date-picker
              class="w-100"
              v-model="sale.date"
              type="date"
              format="dd/MM/yyyy"
              value-format="dd/MM/yyyy"
              placeholder="Pick a date"
            >
            </el-date-picker>

            <label
              for=""
              v-if="store.type !== 'supplier'"
              class="custom-label mt-3 md-text"
              >Type</label
            >
            <div class="flex-align-center" v-if="store.type !== 'supplier'">
              <el-radio v-model="sale.type" class="mr-3" label="In Stock"
                >In Stock</el-radio
              >
              <el-radio v-model="sale.type" label="Pre Order"
                >Pre Order</el-radio
              >
            </div>

            <label for="" class="custom-label mt-3 md-text">Exported By</label>
            <el-select
              v-model="sale.exported_by"
              filterable
              remote
              reserve-keyword
              disabled
              class="w-100"
              placeholder="Select"
            >
            </el-select>

            <label for="" class="custom-label mt-3 md-text">
              {{ store.type === "supplier" ? "Plan" : "Delivery" }}
            </label>
            <el-select
              v-model="sale.delivery_company_name"
              filterable
              remote
              reserve-keyword
              class="w-100"
              allow-create
              placeholder="Select or create new"
              :remote-method="getDeliveries"
              :loading="loadingDelivey"
            >
              <el-option
                v-for="delivery in deliveries"
                :key="delivery.id"
                :label="delivery.name"
                :value="delivery.name"
              >
              </el-option>
            </el-select>

            <label class="custom-label mt-3 mb-0">Note</label>
            <el-input
              type="textarea"
              placeholder="Text such as facebook profile link, etc."
              :row="5"
              v-model="sale.note"
            ></el-input>
          </div>

          <div class="box mt-3" v-if="store.type == 'supplier'">
            <p class="mt-0 mb-2 text-bold">Pricing Details</p>
            <label class="custom-label">Exchange Rate</label>
            <el-input class="w-100" v-model="sale.money_price"></el-input>

            <label class="custom-label mt-3">Service Fees(%)</label>
            <el-input
              class="w-100"
              @change="calcTotal"
              v-model="sale.supplier_percentage"
            ></el-input>

            <label class="custom-label mt-3 mb-0">Order Link</label>
            <el-input id="orderLink" v-model="linkUrl"></el-input>
            <div class="flex-align-start mt-2">
              <el-button class="flat mr-2" @click="copyURL" type="primary"
                >Copy</el-button
              >
              <el-button class="ml-2" @click="openUrl" type="primary"
                >Open Link</el-button
              >
            </div>
          </div>

          <div class="box mt-3 mb-3">
            <p class="mt-0 mb-2 text-bold">Customer</p>
            <label for="" class="custom-label md-text">Name</label>
            <el-select
              v-model="sale.customer_name"
              @change="changeCustomer"
              filterable
              data-is-search="true"
              remote
              allow-create
              reserve-keyword
              class="w-100 mb-3"
              placeholder="Select or create new"
              :remote-method="getCustomers"
              :loading="loadingCustomer"
            >
              <el-option
                v-for="customer in customers"
                :key="customer.id"
                :label="customer.name"
                :value="customer.name"
              >
              </el-option>
            </el-select>

            <label for="" class="custom-label md-text">Phone</label>
            <el-input
              class="w-100 mb-3"
              placeholder="Phone number"
              v-model="sale.phone"
            ></el-input>

            <label for="" class="custom-label md-text">State & city</label>
            <el-cascader
              v-model="address"
              :options="options"
              @change="handleChangeAddress"
              value="item"
            ></el-cascader>
            <label for="Street address" class="mt-3 custom-label"
              >Street address</label
            >
            <el-input
              class="w-100"
              type="textarea"
              :row="5"
              name="Street address"
              placeholder="Street address"
              v-model="sale.address.address"
            ></el-input>
          </div>
        </div>

        <div class="col-12 lg-hide md-hide">
          <!-- <el-button
            v-ripple.400
            type="primary"
            class="mx-0 mb-3 w-100"
            @click="updateStatus('Completed')"
          >
            Mark as Completed
          </el-button> -->

          <el-button
            v-ripple.400
            type="primary"
            :loading="showVoucher"
            class="mx-0 mb-3 w-100"
            @click="changeActions('pdf')"
          >
            Export Voucher
          </el-button>
          <el-button
            v-if="startEditing && !gettingSale"
            :disabled="!startEditing"
            :loading="saving"
            v-ripple.400
            class="w-100 mx-0 mb-3"
            @click="Put"
            type="primary"
          >
            Save
          </el-button>
          <el-button
            v-if="startEditing && !gettingSale"
            @click="discardEditingSale"
            type="primary"
            v-ripple.400
            class="flat w-100 mx-0 mb-3"
          >
            Discard
          </el-button>
        </div>

        <div class="col-12 lg-hide md-hide">
          <el-divider></el-divider>
          <div class="mt-3 mb-3">
            <p class="mt-0 mb-2 text-bold">Activities</p>

            <div
              class="activities mb-3 d-flex"
              v-for="activity in sale.activities"
              :key="activity.text"
            >
              <el-avatar :src="activity.user_avatar"></el-avatar>

              <div class="activity-text ml-2 box" style="max-width: 75vw">
                <p>
                  <span>{{
                    activity.user_first_name + " " + activity.user_last_name
                  }}</span>
                  {{ activity.text }}
                  {{
                    $moment(activity.created_at).format("DD-MM-YYYY hh:mmA")
                  }}.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <el-dialog
      class="default-dialog"
      :show-close="false"
      :visible.sync="openDiscount"
    >
      <template #title>
        <div class="d-flex align-items-center justify-content-between">
          <h5 class="my-0">Add discount</h5>
          <div class="d-flex align-items-center"></div>
        </div>
      </template>
      <div class="con-content">
        <div class="row">
          <div class="col-6 pr-1">
            <label for="discountType" class="pt-1 custom-label"
              >Discount type</label
            >
            <el-select
              v-model="sale.discount_type"
              class="w-100"
              name="discountType"
            >
              <el-option label="Amount" value="$"> </el-option>
              <el-option label="Percentage" value="%"> </el-option>
            </el-select>
          </div>
          <div class="col-6 pl-1">
            <label for="discountType" class="pt-1 custom-label"
              >Discount value</label
            >
            <el-input type="number" class="w-100" v-model="discount"></el-input>
          </div>
          <div class="col-12">
            <label for="reason" class="pt-1 custom-label">Reason</label>
            <el-input
              type="textarea"
              :row="3"
              class="w-100"
              name="reason"
              v-model="sale.discount_reason"
            ></el-input>
          </div>
        </div>
      </div>

      <template #footer>
        <div class="d-flex align-items-center justify-content-end">
          <el-button
            @click="openDiscount = false"
            class="my-0 flat mr-0"
            type="primary"
            v-ripple.400
          >
            Cancel
          </el-button>
          <el-button
            class="my-0 mr-0"
            v-ripple.400
            type="primary"
            @click="calcDiscount"
          >
            Done
          </el-button>
        </div>
      </template>
    </el-dialog>

    <el-dialog
      class="default-dialog"
      :show-close="false"
      :visible.sync="openPayment"
    >
      <template #title>
        <div class="d-flex align-items-center justify-content-between">
          <h5 class="my-0">Make a payment</h5>
          <div class="d-flex align-items-center"></div>
        </div>
      </template>
      <div class="con-content">
        <div class="row">
          <div class="col-12">
            <label for="" class="custom-label mt-1 md-text">Payment Type</label>
            <div class="flex mt-2 mb-3 flex-x-between">
              <div
                class="payment-type"
                :class="payment.type == 'KPay' && 'selected-payment'"
                @click="payment.type = 'KPay'"
              >
                <img src="/image/KPay.png" alt="" />
              </div>

              <div
                class="payment-type"
                :class="payment.type == 'Wave Pay' && 'selected-payment'"
                @click="payment.type = 'Wave Pay'"
              >
                <img src="/image/Wave Pay.png" alt="" />
              </div>

              <div
                class="payment-type"
                :class="payment.type == 'Cash' && 'selected-payment'"
                @click="payment.type = 'Cash'"
              >
                <img src="/image/Cash.png" alt="" />
              </div>

              <div
                class="payment-type"
                :class="payment.type == 'COD' && 'selected-payment'"
                @click="payment.type = 'COD'"
              >
                <img src="/image/COD.png" alt="" />
              </div>
            </div>
          </div>
          <div class="col-6 pr-1">
            <div class="w-100">
              <label for="" class="custom-label mt-1 md-text">Date</label>
              <el-date-picker
                class="w-100"
                v-model="payment.date"
                type="date"
                format="dd/MM/yyyy"
                value-format="dd/MM/yyyy"
                placeholder="Pick a date"
              >
              </el-date-picker>
            </div>
          </div>
          <div class="col-6 pl-1">
            <label for="" class="custom-label mt-1 md-text">Amount</label>
            <el-input
              class="w-100"
              label=""
              placeholder="0"
              type="number"
              v-model="payment.amount"
            />
          </div>
        </div>
      </div>

      <template #footer>
        <div class="d-flex align-items-center justify-content-end">
          <el-button
            @click="openPayment = false"
            class="my-0 flat mr-0"
            type="primary"
            v-ripple.400="'rgba(var(--primary),0.2)'"
          >
            Cancel
          </el-button>
          <el-button
            class="my-0 mr-0"
            v-ripple.400
            type="primary"
            :loading="adding"
            @click="addPayment"
          >
            Done
          </el-button>
        </div>
      </template>
    </el-dialog>

    <el-dialog
      class="default-dialog"
      :show-close="false"
      :visible.sync="openFulfillDialog"
    >
      <template #title>
        <div class="d-flex align-items-center justify-content-between">
          <h5 class="my-0">Fulfill items</h5>
          <div class="d-flex align-items-center"></div>
        </div>
      </template>
      <div class="con-content">
        <div class="row" v-for="(tr, index) in sale.products" :key="tr.id">
          <div class="col-8 flex-x-between">
            <div class="flex-align-center">
              <img
                v-lazy="mediaUrl + tr.image"
                class="simple-image list-image"
                alt=""
              />
              <p>
                {{ tr.name }}
              </p>
            </div>
          </div>
          <div class="col-4 flex-align-center flex-end">
            <el-input-number
              class="w-50"
              v-model="tr.number_of_fullfilled"
              type="number"
              @change="changeFulfill(index)"
              :min="0"
              :max="parseFloat(tr.quantity)"
              controls-position="right"
            ></el-input-number>
            <p class="ml-2">of {{ tr.quantity }}</p>
          </div>
          <div class="col-12" v-if="index !== sale.products.length - 1">
            <div class="b-divider"></div>
          </div>
        </div>
      </div>

      <template #footer>
        <div class="d-flex align-items-center justify-content-end">
          <el-button
            @click="cancelFulfill"
            class="my-0 flat mr-0"
            type="primary"
            v-ripple.400="'rgba(var(--primary),0.2)'"
          >
            Cancel
          </el-button>
          <el-button
            class="my-0 mr-0"
            v-ripple.400
            type="primary"
            :loading="addingFulfill"
            @click="addFulfill"
          >
            Done
          </el-button>
        </div>
      </template>
    </el-dialog>
    <voucher :sale="sale" v-if="showVoucher" />
  </div>
</template>

<script>
import { mapState } from "vuex";
import address from "../../../core/constant/address";
import { url, mediaUrl } from "../../../core/constant/data";
import { file, empty, voucher } from "../../common";
import domtoimage from "dom-to-image-more";
export default {
  data() {
    return {
      url: url,
      showPrint: "address",
      active: 1,
      options: address,
      openCustomProduct: false,
      mediaUrl: mediaUrl,
      openFulfillDialog: false,
      addingFulfill: false,
      openPayment: false,
      payment: {
        type: "KPay",
        amount: "0",
        date: "",
        sale_id: null,
      },
      customProduct: {
        name: "",
        is_custom_product: true,
        quantity: "1",
        price: "0",
        number_of_stock: 100000,
        image: "images/default.png",
        variation_product: null,
        product: null,
        subtotal: "0",
      },
      customers: [],
      deliveries: [],
      inventory: [],
      removed_ids: [],
      loadingProduct: false,
      products: [],
      loadingCustomer: false,
      loadingDelivey: false,
      address: [],
      tax: 5,
      discount: 0,
      selectedProduct: "",
      openDiscount: false,
      saving: false,
      gettingSale: false,
      sale: {
        payment_status: "Unpaid",
        supplier_percentage: "5",
        money_price: "300",
        customer_name: "",
        location_name: "",
        type: "Instock",
        exported_by: "",
        status: "Active",
        tax_rate: 5,
        delivery_company_name: null,
        delivery_date: null,
        payment_method: null,
        location: 1,
        payments: [],
        address: { state: "", city: "", address: "", map: "" },
        products: [],
        timelines: [],
        subtotal: "",
        total: "0",
        discount: "0",
        discount_reason: "",
        discount_type: "$",
        is_same_address: false,
        tax: "",
        note: "",
        tags: [],
      },
      selectedCustomer: {},
      loading: null,
      startEditing: false,
      adding: false,
      due_amount: 0,
      query: "",
      voucher: {
        type: "pdf",
      },
      generating: false,
      linkUrl: "",
      showVoucher: false,
      productQuery: {},
    };
  },
  components: { file, empty, voucher },
  watch: {
    sale: {
      handler: function () {
        this.startEditing = true;
      },
      deep: true,
    },
    productQuery(val) {
      this.sale.products.push({
        quantity: "1",
        sale_price: val.price,
        subtotal: val.price,
        name: val.name,
        image: val.image,
        primary_price: "0",
        primary_price_myanmar: val.cost_per_item,
        number_of_fullfilled: 0,
        product_id: val.id,
      });
    },
    locations: {
      handler: function (value) {
        this.sale.location = value[0].id;
        this.sale.location_name = value[0].name;
      },
      deep: true,
    },
    "sale.status": function (data) {
      if (data == "Active") {
        this.active = 1;
      } else if (data == "Payment Paid") {
        this.active = 2;
      } else if (data == "Checked Products") {
        this.active = 3;
      } else if (data == "Packed a Parcel") {
        this.active = 4;
      } else if (data === "Delivery Pickup Done") {
        this.active = 5;
      } else {
        this.active = 1;
      }
    },
  },
  mounted() {
    this.$store.commit("setIsReady", false);
    var print = localStorage.getItem("print");
    if (print) {
      this.showPrint = print;
    } else {
      this.showPrint = "address";
    }
    this.filterSale();
  },
  beforeMount() {
    window.addEventListener("beforeunload", this.preventNav);
    this.$once("hook:beforeDestroy", () => {
      window.removeEventListener("beforeunload", this.preventNav);
    });

    document.addEventListener("scan", this.onScan);
    this.$once("hook:beforeDestroy", () => {
      document.removeEventListener("scan", this.onScan);
    });
  },
  beforeDestroy() {
    document.removeEventListener("scan", this.onScan);
  },
  beforeRouteLeave(to, from, next) {
    document.removeEventListener("scan", this.onScan);
    if (this.startEditing) {
      this.$smalltalk
        .confirm(
          "Question",
          "Do you really want to leave? you have unsaved changes!"
        )
        .then(() => {
          next();
        })
        .catch(() => {
          next(false);
        });
    } else {
      next();
    }
  },
  methods: {
    onScan({ detail }) {
      this.loadingProduct = true;
      if (detail.scanCode) {
        this.$axios
          .get("product/search/?query=" + detail.scanCode)
          .then((response) => {
            this.loadingProduct = false;

            if (response.data.length > 0) {
              var val = response.data[0];
              var check = this.sale.products.filter(
                (item) => item.product_id == val.id
              );

              if (check.length > 0) {
                this.sale.products.map((item) => {
                  if (item.product_id == val.id) {
                    item.quantity = item.quantity + 1;
                  }
                });
              } else {
                this.sale.products.push({
                  quantity: "1",
                  sale_price: val.price,
                  subtotal: val.price,
                  name: val.barcode,
                  image: val.image,
                  primary_price: "0",
                  primary_price_myanmar: val.cost_per_item,
                  number_of_fullfilled: 0,
                  product_id: val.id,
                });
              }
            } else {
              this.$noti.display({
                message: "No products found with " + detail.scanCode,
                title: "",
                status: "success",
                color: "error",
              });
            }
          })
          .catch((err) => {
            this.loadingProduct = false;
            this.$noti.display({
              message: err,
              title: "",
              status: "error",
              color: "error",
            });
          });
      }
    },
    formatString(str) {
      var str_ = str.split("_").join(" ");
      str_ = str_.split("(").pop().replace(")", "");
      return str_;
    },
    preventNav(event) {
      if (!this.startEditing) return;
      event.preventDefault();
      event.returnValue = "";
    },
    openUrl() {
      window.open(this.linkUrl, "_blank").focus();
    },
    copyURL() {
      navigator.clipboard.writeText(this.linkUrl);
      this.isCopied = true;
      this.$noti.display({
        color: "success",
        message: "Copied to clipboard!",
      });
    },
    handleChangeAddress(address) {
      this.sale.address.state = address[0];
      this.sale.address.city = address[1];
    },
    openPaymentDialog() {
      this.payment.date = this.$moment().format("DD/MM/YYYY");
      this.payment.amount = this.sale.due_amount;
      this.openPayment = true;
    },
    changeActions(command) {
      if (command == "image" || command == "pdf") {
        this.voucher.type = command;
        this.generate();
      } else if (command == "Print") {
        this.showPrint = "address";
        localStorage.setItem("print", "address");
        setTimeout(() => {
          window.print();
        }, 100);
      } else if (command == "Slip") {
        this.showPrint = "slip";
        localStorage.setItem("print", "slip");

        setTimeout(() => {
          window.print();
        }, 100);
      } else if (command == "printCode") {
        this.printPdf();
      } else {
        this.updateStatus(command);
      }
    },
    updateStatus(status) {
      this.updatingStatus = true;
      this.$axios
        .put("sales-status/", {
          status: status,
          ids: [this.sale.id],
        })
        .then(() => {
          this.sale.status = status;
          this.saleBeforeEdit = JSON.stringify(this.sale);
          setTimeout(() => {
            this.editingCustomer = false;
            this.startEditing = false;
            this.updatingStatus = false;
          }, 10);
        })
        .catch((err) => {
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
          this.updatingStatus = false;
        });
    },
    changeStatus(status) {
      this.updatingStatus = true;
      this.$axios
        .put("sales-status/", {
          status: status,
          text: `has changed Order Status to "${status}" at`,
          ids: [this.sale.id],
        })
        .then(() => {
          this.sale.status = status;
          this.saleBeforeEdit = JSON.stringify(this.sale);
          setTimeout(() => {
            this.editingCustomer = false;
            this.startEditing = false;
            this.updatingStatus = false;
          }, 10);
        })
        .catch((err) => {
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
          this.updatingStatus = false;
        });
    },
    removePayment(index, id) {
      this.updatingStatus = true;
      this.$axios
        .put("payment/", {
          id: id,
        })
        .then((res) => {
          this.sale = res.data;
          this.saleBeforeEdit = JSON.stringify(res.data);
          setTimeout(() => {
            this.editingCustomer = false;
            this.startEditing = false;
            this.updatingStatus = false;
          }, 10);
        })
        .catch((err) => {
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
          this.updatingStatus = false;
        });
    },
    printPdf() {
      var tableRows = "";
      var data = [];
      this.sale.products.forEach((element) => {
        if (element.link) {
          var check = data.filter((da) => da.link == element.link);
          if (check.length == 0) {
            data.push(element);
          }
        }
      });

      data.forEach((order, index) => {
        tableRows += `<tr>
                          <td>${index + 1}</td>
                          <td>${order.link}</td>
                          <td>${order.created_at}</td>
                      </tr>`;
      });
      var html = `
                <!DOCTYPE html>
                <html lang="en">
                  <head>
                    <meta charset="UTF-8" />
                    <meta http-equiv="X-UA-Compatible" content="IE=edge" />
                    <meta name="viewport" content="width=device-width, initial-scale=1.0" />
                    <title>Order Codes</title>
                    <style>
                      table {
                        font-family: arial, sans-serif;
                        border-collapse: collapse;
                        width: 100%;
                        margin-bottom: 15px;
                      }

                      td,
                      th {
                        border: 1px solid #dddddd;
                        text-align: left;
                        padding: 8px;
                      }
                    </style>
                  </head>
                  <body>
                    <table>
                      <thead>
                        <tr>
                          <th style="width:50px;">No.</th>
                          <th>Code</th>
                          <th>Date</th>
                        </tr>
                      </thead>
                      <tbody id="table-body">${tableRows}</tbody>
                    </table>
                  </body>
                </html>
`;
      var newWin = window.open("", "Print-Window");
      newWin.document.open();
      newWin.document.write(html);
      newWin.window.print();
    },
    isFulfilled(product) {
      return (
        parseFloat(product.quantity) ===
        parseFloat(product.number_of_fullfilled)
      );
    },
    getProducts(query) {
      this.loadingProduct = true;
      if (query === "" || !query) {
        query = "Null";
      }
      this.$axios
        .get("product/search/?query=" + query)
        .then((response) => {
          this.loadingProduct = false;
          this.products = response.data;
        })
        .catch((err) => {
          this.loadingProduct = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    changeFulfill(index) {
      this.sale.products[index].is_fulfilled =
        parseFloat(this.sale.products[index].quantity) ===
        parseFloat(this.sale.products[index].number_of_fullfilled);
    },
    addFulfill() {
      this.addingFulfill = true;
      var data = [];
      var is_fulfilled = true;
      this.sale.products.forEach((value) => {
        value.is_fulfilled =
          parseFloat(value.quantity) === parseFloat(value.number_of_fullfilled);
        data.push({
          is_fulfilled: value.is_fulfilled,
          number_of_fullfilled: value.number_of_fullfilled,
          id: value.id,
        });
        if (!value.is_fulfilled) {
          is_fulfilled = value.is_fulfilled;
        }
      });
      this.sale.is_fulfilled = is_fulfilled;
      this.$axios
        .put("fulfill/", { data, sale_id: this.sale.id, is_fulfilled })
        .then(() => {
          this.addingFulfill = false;
          this.openFulfillDialog = false;
          this.saleProductsBeforeEdit = JSON.stringify(this.sale.products);
          this.$noti.display({
            title: "Success",
            color: "success",
            message: "Cool! Fulfillment has been successfully added.",
          });
        })
        .catch((err) => {
          this.addingFulfill = false;
          this.openFulfillDialog = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    openFulfill() {
      this.saleProductsBeforeEdit = JSON.stringify(this.sale.products);
      this.openFulfillDialog = true;
    },
    cancelFulfill() {
      this.sale.products = JSON.parse(this.saleProductsBeforeEdit);
      setTimeout(() => {
        this.startEditing = false;
      }, 10);
      this.openFulfillDialog = false;
    },
    addPayment() {
      this.adding = true;
      this.payment.sale_id = this.sale.id;
      this.payment.payment_status = this.sale.payment_status;
      this.$axios
        .post("payment/", {
          data: this.payment,
        })
        .then((response) => {
          this.sale = response.data;
          this.openPayment = false;
          this.adding = false;

          this.payment = {
            sale_id: this.sale.id,
            amount: "",
            type: "KPay",
            date: this.$moment().format("dd/MM/yyyy"),
          };

          setTimeout(() => {
            this.editingCustomer = false;
            this.startEditing = false;
            this.updatingStatus = false;
          }, 10);
        })
        .catch((err) => {
          this.openPayment = false;
          this.adding = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    filterSale() {
      this.gettingSale = true;
      this.$axios
        .get("sale/?sid=" + this.$route.params.id)
        .then((response) => {
          this.sale = response.data;
          this.linkUrl = `https://osassist-orders.netlify.app/?sid=${this.store.id}&oid=${response.data.id}&date=${response.data.date}`;
          this.saleBeforeEdit = JSON.stringify(this.sale);
          var is_fulfilled = true;
          this.address = [
            response.data.address.state,
            response.data.address.city,
          ];
          this.sale.products.forEach((product) => {
            product.is_fulfilled =
              parseFloat(product.quantity) ===
              parseFloat(product.number_of_fullfilled);
            if (!product.is_fulfilled) {
              is_fulfilled = false;
            }
          });
          if (this.store.type === "supplier") {
            this.sale.by = (
              parseFloat(this.sale.total) * parseInt(this.sale.money_price)
            ).toFixed(0);

            this.calcProfitAmount();
          }
          this.sale.is_fulfilled = is_fulfilled;
          setTimeout(() => {
            this.startEditing = false;
            this.gettingSale = false;
          }, 10);
          this.$store.commit("setIsReady", true);
        })
        .catch((err) => {
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });

      // this.calcDueAmount();
    },
    calcDueAmount() {
      this.sale.due_amount =
        parseFloat(this.sale.total) -
        parseFloat(
          this.sale.payments.reduce((total, item) => {
            return total + parseFloat(item.amount);
          }, 0)
        );
      if (parseFloat(this.sale.due_amount) === 0) {
        this.sale.payment_status = "Paid";
      } else if (
        parseFloat(this.sale.due_amount) === parseFloat(this.sale.total)
      ) {
        this.sale.payment_status = "Unpaid";
        this.active = 1;
      } else {
        this.sale.payment_status = "Partially Paid";
        this.active = 1;
      }
      setTimeout(() => {
        this.startEditing = false;
      }, 10);
    },
    setImage($event, index) {
      if ($event.length > 0) {
        this.sale.products[index].image = $event[0].image;
      }
    },
    addItem() {
      this.sale.products.push({
        quantity: "1",
        sale_price: "",
        subtotal: "",
        name: "",
        image: "/media/default.png",
        link: null,
        primary_price: null,
        primary_price_myanmar: null,
        number_of_fullfilled: 0,
        product_id: "",
      });
      this.calcSubtotal();
    },
    removeProduct(index, id) {
      this.sale.products.splice(index, 1);
      this.removed_ids.push(id);
      this.calcSubtotal();
    },
    getCustomers(query) {
      this.loadingCustomer = true;
      if (query === "" || !query) {
        query = "Null";
      }
      this.$axios
        .get("search-customer/?query=" + query)
        .then((response) => {
          this.loadingCustomer = false;
          this.customers = response.data;
        })
        .catch((err) => {
          this.loadingCustomer = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    getDeliveries(query) {
      this.loadingDelivery = true;
      if (query === "" || !query) {
        query = "Null";
      }
      this.$axios
        .get("search-delivery/?query=" + query)
        .then((response) => {
          this.loadingDelivery = false;
          this.deliveries = response.data;
        })
        .catch((err) => {
          this.loadingDelivery = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    changeCustomer() {
      var check = this.customers.filter((data) => {
        return data.name == this.sale.customer_name;
      });
      if (check.length > 0) {
        this.selectedCustomer = check[0];
        this.sale.phone = this.selectedCustomer.phone;
        if (this.selectedCustomer.address) {
          this.sale.address = this.selectedCustomer.address;
          this.address = [
            this.selectedCustomer.address.state,
            this.selectedCustomer.address.city,
          ];
        }
      }
    },
    calcDiscount() {
      if (this.sale.discount_type === "%") {
        this.sale.discount =
          (parseFloat(this.discount) / 100) * parseFloat(this.sale.subtotal);
      } else {
        this.sale.discount = parseFloat(this.discount);
      }
      this.openDiscount = false;
      this.calcTotal();
    },
    calcTotal() {
      if (this.store.type === "supplier") {
        this.sale.total = (
          parseFloat(this.sale.subtotal) +
          parseFloat(this.sale.subtotal) *
            (parseFloat(this.sale.supplier_percentage) / 100) -
          parseFloat(this.sale.discount)
        ).toFixed(0);

        this.sale.by = (
          parseFloat(this.sale.total) * parseInt(this.sale.money_price)
        ).toFixed(0);

        this.calcProfitAmount();
      } else {
        this.sale.total = (
          parseFloat(this.sale.subtotal) - parseFloat(this.sale.discount)
        ).toFixed(0);
      }
    },

    calcSubtotal() {
      this.sale.subtotal = this.sale.products
        .reduce((total, item) => {
          return total + parseFloat(item.subtotal);
        }, 0)
        .toFixed(2);
      this.calcDiscount();
    },
    calcProfitAmount() {
      if (parseInt(this.sale.purchase_amount) !== 0) {
        this.sale.profit_amount = parseInt(
          parseFloat(this.sale.by) - parseFloat(this.sale.purchase_amount)
        ).toFixed(0);
      } else {
        this.sale.profit_amount = 0;
      }
    },
    changeQuantity(index) {
      this.sale.products[index].subtotal = (
        parseFloat(this.sale.products[index].sale_price) *
        parseFloat(this.sale.products[index].quantity)
      ).toFixed(2);
      this.calcSubtotal();
    },
    calcPrice(index) {
      if (
        this.sale.products[index].sale_price &&
        this.sale.products[index].primary_price_myanmar
      ) {
        this.sale.products[index].profit = parseFloat(
          this.sale.products[index].sale_price -
            this.sale.products[index].primary_price_myanmar
        );
        this.sale.products[index].margin = (
          (this.sale.products[index].profit /
            this.sale.products[index].sale_price) *
          100
        ).toFixed(2);
      } else {
        this.sale.products[index].margin = null;
        this.sale.products[index].profit = null;
      }
      this.changeQuantity(index);
    },
    calcAllProductPrice() {
      var data = [];
      this.sale.products.forEach((product, index) => {
        product.primary_price_myanmar = (
          (parseFloat(product.primary_price) *
            (parseFloat(this.sale.supplier_percentage) / 100) +
            parseFloat(product.primary_price)) *
          this.sale.money_price
        ).toFixed(0);
        this.calcPrice(index);
        data.push(product);
      });
      var dataString = JSON.stringify(data);
      setTimeout(() => {
        this.sale.products = [];
        this.sale.products = JSON.parse(dataString);
      }, 500);
    },
    changePrimaryPrice($event, index) {
      const supplier_percentage =
        parseFloat(this.sale.supplier_percentage) / 100;
      this.sale.products[index].primary_price_myanmar = parseFloat(
        (parseFloat($event) * supplier_percentage + parseFloat($event)) *
          this.sale.money_price
      );
      var string = JSON.stringify(this.sale.products);
      this.sale.products = [];
      this.sale.products = JSON.parse(string);
      this.calcPrice(index);
    },
    Put() {
      this.saving = true;
      this.sale.store = this.store.id;
      this.$axios
        .put("sale/", { data: this.sale, removed_ids: this.removed_ids })
        .then((res) => {
          this.sale = res.data;
          this.saleBeforeEdit = JSON.stringify(this.sale);
          this.removed_ids = [];
          this.sale.products.forEach((product) => {
            product.is_fulfilled =
              parseFloat(product.quantity) ===
              parseFloat(product.number_of_fullfilled);
          });
          this.calcDueAmount();
          setTimeout(() => {
            this.startEditing = false;
            this.adding = false;
            this.saving = false;
          }, 1);
          this.openPayment = false;
          this.$noti.display({
            title: "Success",
            color: "success",
            message: "Cool! Sale has been successfully added.",
          });
        })
        .catch((err) => {
          this.saving = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    discardEditingSale() {
      this.sale = JSON.parse(this.saleBeforeEdit);
      setTimeout(() => {
        this.startEditing = false;
      }, 10);
    },
    generate() {
      this.showVoucher = true;
      setTimeout(() => {
        const node = document.querySelector(".invoice-body");
        domtoimage
          .toPng(node, {
            width: node.clientWidth * 5,
            height: node.clientHeight * 5,
            style: {
              transform: "scale(" + 5 + ")",
              transformOrigin: "top left",
            },
          })
          .then((dataUrl) => {
            const link = document.createElement("a");
            link.href = dataUrl;
            link.setAttribute(
              "download",
              this.sale.customer_name + "-voucher.png"
            ); //or any other extension
            document.body.appendChild(link);

            link.click();
            if (
              window.ReactNativeWebView &&
              window.ReactNativeWebView.postMessage
            ) {
              window.ReactNativeWebView.postMessage(dataUrl);
            }
            this.showVoucher = false;
          })
          .catch(() => {
            this.showVoucher = false;
          });
      }, 500);
    },
    changeIsSame() {
      if (this.sale.is_same_address) {
        this.sale.shipping_address = this.sale.address;
      } else {
        this.sale.shipping_address = {
          state: "",
          city: "",
          address: "",
          map: "",
        };
      }
    },
  },
  computed: {
    ...mapState(["isReady", "store"]),
  },
};
</script>
